import React from 'react';
import data from '../Data';

import CarSelector from './CarSelector';
import CompareTable from './CompareTable';


class CarCompareBox extends React.Component {
    state = {
        brands          : [],

        displayTable    : false,

        a_id            : 0,
        b_id            : 0,
        c_id            : 0,

        a_data          : {},
        b_data          : {},
        c_data          : {}
    }

    compareClick = () => {
        const { a_id, b_id, c_id } = this.state;

        if (a_id === 0 && b_id === 0 && c_id === 0) {
            // don't show table, which will be blank if shown
        } else {
            this.setState({ displayTable : true });
        }
        
        
        if (a_id > 0) {
            fetch(`${data.api}api/vehicle/${a_id}/`)
                .then(response => response.json())
                .then(data => this.setState({
                    a_data : data
                }))
        }

        if (b_id > 0) {
            let url = `${data.api}api/vehicle/${b_id}/`;
            fetch(url)
                .then(response => response.json())
                .then(data => this.setState({
                    b_data : data
                }))
        }

        if (c_id > 0) {
            fetch(`${data.api}api/vehicle/${c_id}/`)
                .then(response => response.json())
                .then(data => this.setState({
                    c_data : data
                }))
        }
    }

    getVehicleId = (column, brand, model, variant) => {
        let idVar = column + '_id';
        fetch(`${data.api}api/${variant}/`)
        .then(response => response.json())
        .then(data => this.setState({
            [idVar] : data.id
        }))
    }

    componentDidMount() {
        fetch(`${data.api}api/brands/car/`)
            .then(response => response.json())
            .then(brands => this.setState({ brands : brands }))
    }

    render() {
        const { a_data, b_data, c_data, displayTable } = this.state;
        return (
            <div className="container">
                <table className="table">
                <tbody className="compareControl">
                    <tr>
                        <td></td>
                        <th className="text-center">1st Car</th>
                        <th className="text-center">2nd Car</th>
                        <th className="text-center">3rd Car</th>
                    </tr>
                    <tr>
                        <td></td>
                        <CarSelector column='a' brands={ this.state.brands } getVehicleId={ this.getVehicleId } />
                        <CarSelector column='b' brands={ this.state.brands } getVehicleId={ this.getVehicleId } />
                        <CarSelector column='c' brands={ this.state.brands } getVehicleId={ this.getVehicleId } />
                    </tr>
    
                    <tr>
                        <td colSpan="4">
                            <button onClick={ this.compareClick } className="btn btn-secondary col-sm-4">Compare</button>
                        </td>
                    </tr>
                </tbody>
                </table>
            
                { displayTable ? <CompareTable a_data={ a_data } b_data={ b_data } c_data={ c_data } /> : null }
            </div>
        );
    }
}

export default CarCompareBox;


