import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';

import Compare from './compare/Compare';
import Home from './home/Home';
import Search from './search/Search';
import VehicleDetail from './vehicledetail/VehicleDetail';


class Main extends Component {
    render() {
        const extra = {
            query : this.props.query,
            submit : this.props.submit,
            submitFalse : this.props.submitFalse
        }
        return (
            <div style={{marginBottom :70}}> {/* margin bottom provided to make space for footer */}
                <Switch>
                    <Route path='/compare' component={Compare} />
                    <Route exact path='/' component={Home} />
                    <Route path={`/search`} render={(props) => <Search {...props} {...extra} />} />
                    <Route path={`/:vehicleId`} component={VehicleDetail} />
                </Switch>
            </div>
        );
    }
}

export default Main;


