import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router } from 'react-router-dom';

import Navbar from './Navbar';
import Main from './Main';
import Footer from './Footer';


class App extends Component {
  state = {
      query : '',
      submit : false
  }

  submitFalse = (e) => {
      this.setState({
          submit : false
      })
  }

  submitTrue = (query) => {
      console.log('query: ' + query)
      this.setState({
          query : query,
          submit : true
      })
  }

  render() {
    return (
      <Router>
        <div>
          <Navbar submitTrue={this.submitTrue}/>
          <Main submit={this.state.submit} submitFalse={this.submitFalse} query={this.state.query}/>
          <Footer/>
        </div>
      </Router>
    );
  }
}

export default App;



